import "./profile.css";
import Nav from "../components/Navbar/Nav";
import { useAuth } from "../Context/auth";
import UserMenu from "./userMenu";
import user from "../Images/Admin/Admin.png";
import { NavLink } from "react-router-dom";
import {
  FcGoogle,
  FcGraduationCap,
  FcSalesPerformance,
  FcBriefcase,
} from "react-icons/fc";

const UserProfile = () => {
  const [auth] = useAuth();

  return (
    <div>
      <Nav />

      <div className="user_main">
        <div className="user_inner_main ">
          <hr />
          <div className="d-flex row container-fluid p-3 ">
            <div className="col-lg-2 col-sm-12 ">
              <UserMenu />
            </div>
            <div className="col-lg-10 col-sm-12  ">
              <div
                className="   p-3 w-80 ps-5  d-flex flex-row  rounded-2 "
                style={{ boxShadow: " rgba(0, 0, 0, 0.35) 0px 5px 15px" }}
              >
                <div className=" text-center p-3 rounded-5">
                  {" "}
                  <img
                    className=" rounded-5"
                    src={
                      `/api/auth/userphoto/${auth.user._id}`
                        ? `/api/auth/userphoto/${auth.user._id}`
                        : user
                    }
                    width={250}
                    title={auth?.user?.userName ? auth.user.userName : "user"}
                    alt={"user_Photo"}
                  />
                </div>
                <div>
                  <h3 className="p-3 text-uppercase">
                    {" "}
                    name :{" "}
                    <span className="text-muted"> {auth?.user?.userName}</span>
                  </h3>
                  <h3 className="p-3 text-uppercase">
                    {" "}
                    email :{" "}
                    <span className="text-muted">{auth?.user?.email}</span>
                  </h3>

                  <h3 className="p-3 text-uppercase">
                    {" "}
                    phone :{" "}
                    <span className="text-muted">{auth?.user?.phone}</span>
                  </h3>
                  <h3 className="p-3 text-uppercase">
                    {" "}
                    address :{" "}
                    <span className="text-muted">{auth?.user?.address}</span>
                  </h3>
              
                </div>
              </div>
            </div>
            <div className="d-flex gap-4 flex-row mt-3 justify-content-center">
              <NavLink to="\">
                <FcGoogle className="h2" />
              </NavLink>
              <NavLink to="\">
                <FcGraduationCap className="h2" />
              </NavLink>
              <NavLink to="\">
                <FcSalesPerformance className="h2" />
              </NavLink>
              <NavLink to="\">
                <FcBriefcase className="h2" />
              </NavLink>
            </div>
          </div>
        </div>
      </div>

      <br />
    </div>
  );
};

export default UserProfile;
